body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

p {
  padding: 0;
}
.header {
    margin: 20px auto 40px;
    width: 800px;
    font-size: 24px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    box-shadow: 5px 5px 10px #333131;
}

.header__logo {
    text-decoration: none;
    color: #000000;
}

.header__logo:hover {
    color: #473d3d;
}

.header__list {
    width: 400px;
    display: flex;
    justify-content: space-between;
}

.header__link {
    text-decoration: none;
    color: #000000;
}

.header__link:hover {
    color: #473d3d;   
}

.user__text {
    cursor: pointer;
}

.user__text:hover {
    color: #473d3d;
}
.table {
    margin: 0 auto 30px;
    width: 650px;
    text-align: center;
    border-collapse: collapse;
    font-size: 20px;
}

.table__caption {
    margin-bottom: 10px;
    font-size: 22px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-style: italic;
}

tr, th, td {
    border: 1px solid #000000;
}

th, td {
    padding: 5px;
}
.dish {
    box-sizing: border-box;
    width: 280px;
    padding: 5px;
    box-shadow: 3px 3px 10px #242020;
    margin-bottom: 20px;
}

.dish__name {
    font-size: 20px;
    font-weight: 600;
    text-align: center;
}

.dish__img {
    display: block;
    max-width: 100%;
    height: auto;
}


.comment {
    border: 1px solid #1b1a1a;
    padding: 5px;
    margin-bottom: 5px;
}

.comment__rate {
    margin: 0 0 5px;
}

.comment__text {
    margin: 0;
}
.comment-area {
    box-sizing: border-box;
    width: 280px;
}

.comment-form {
    margin: 0 auto 20px;
    width: 200px;
    display: flex;
    flex-direction: column;
}

.comment-form__textarea {
    resize: none;
}

.comment-form__btn {
    cursor: pointer;
    border: none;
    padding: 5px;
    background-color: #4e473d;
    color: #eeeceb;
}

.comment-form__btn:hover {
    background-color: #eeeceb;
    color: #4e473d;
}

.comment-form__select {
    padding: 4px;
}
.dish-menu {
    margin-bottom: 30px;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    list-style: none;
}
.filter {
    font-size: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 230px;
    margin: 0 auto 20px;
}

.filter__input {
    width: 150px;
    font-size: 20px;
}
.form {
    font-size: 20px;
    margin: 0 auto 30px;
    width: 200px;
    display: flex;
    flex-direction: column;
}

.form__input {
    padding: 4px;
}

.form__btn {
    border: none;
    background-color: #1a1616;
    color: #daf8f8;
    padding: 4px;
    cursor: pointer;
}

.form__btn:hover {
    background-color: #daf8f8;
    color: #1a1616;
}
