.dish {
    box-sizing: border-box;
    width: 280px;
    padding: 5px;
    box-shadow: 3px 3px 10px #242020;
    margin-bottom: 20px;
}

.dish__name {
    font-size: 20px;
    font-weight: 600;
    text-align: center;
}

.dish__img {
    display: block;
    max-width: 100%;
    height: auto;
}

